.BlurSphere {
  position: absolute;
  border-radius: 9999px;
  backdrop-filter: blur(5px);
  background-color: rgba(255,255,255,0.05);
  z-index: 8;
  animation: float var(--duration) linear infinite;
}

@keyframes float {
  0% {
    transform: translateX(0rem) translateY(0rem)
  }
  20% {
    transform: translateX(var(--xA)) translateY(var(--yA))
  }
  40% {
    transform: translateX(var(--xB)) translateY(var(--yB))
  }
  60% {
    transform: translateX(var(--xC)) translateY(var(--yC))
  }
  80% {
    transform: translateX(var(--xD)) translateY(var(--yD))
  }
  100% {
    transform: translateX(0rem) translateY(0rem)
  }
}